body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.App{height:100vh}.App .Background-Image{position:absolute;background-image:url(/markdueck/static/media/background_cropped.3b9542f7.jpg);background-repeat:no-repeat;background-position:center;z-index:-1;height:100%;width:100%;object-fit:cover}

.App-Header{display:flex;position:fixed;width:100%}.App-Header button{background:orange;border:none;object-fit:cover;max-width:24px;height:24px;width:100%;margin:2px;padding:2px;-webkit-transition:all .15s ease-in-out;transition:all .15s ease-in-out;border-radius:2px}.App-Header button:focus{outline:none}.App-Header button:active{background-color:#d88c00}.App-Header button:hover{cursor:pointer}.App-Header button img{object-fit:contain;max-height:100%;max-width:100%}.App-Header .Menu-Btn{margin-left:auto}

.App-Body{display:flex;height:100%;align-items:center;justify-content:center}.App-Body button{background:orange;color:white;border:none;object-fit:cover;height:3em;width:15em;-webkit-transition:all .15s ease-in-out;transition:all .15s ease-in-out;border-radius:2px}.App-Body button:focus{outline:none}.App-Body button:active{background-color:#d88c00;color:#2468cf}.App-Body button:hover{-webkit-transition:1s;transition:1s;color:#2468cf;cursor:pointer}.nav-btn{position:relative;background:none;border:none;border-left:1px solid #c8c8c8;width:100%;font:inherit}.nav-btn:first-child{border-left:none}.nav-btn:focus{outline:none}.nav-btn.btn-slide-line.center:after{left:50%}.nav-btn.btn-slide-line:after{position:absolute;-webkit-transition:.5s;transition:.5s;content:'';width:0;bottom:0;height:5px;background:#2468cf}.nav-btn.btn-slide-line:hover{cursor:pointer}.nav-btn.btn-slide-line:hover:after{width:100%;left:0}

.App-Footer{position:fixed;bottom:0;width:100%}

